.quick-polygraphy {
  background-image: url("/img/firstyle.png");
  background-size: 304px 255px;
  background-position: 57% 62.2%;
  background-repeat: no-repeat;
  background-color: #faf8fb;
}

.quick-polygraphy__title {
  width: 96%;
  height: 84px;
  font-family: @muller;
  font-size: 24px;
  line-height: 28px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: -0.5px;

  color: #383d52;
  padding-top: 70px;

  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    left: 15px;
    top: 24px;
    width: 142px;
    height: 174px;
    background-image: url("/img/number-three.png");
    z-index: -1;
  }
}

.quick-polygraphy__description {
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.4px;

  color: #5e606a;
  margin-top: 50px;

  &--second-paragraph {
    margin-top: 23px;
  }
}

.form-block {
  margin-top: 311px;
}

.form-block__title {
  font-family: @openSans;
  font-size: 16px;
  font-style: italic;
  font-weight: normal;
  line-height: 30px;

  color: #383d52;
}

.form-block__subtitle {
  width: 68%;
  font-family: @openSans;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.5px;

  color: #383d52;
}

.service-form__input-name,
.service-form__input-phone {
  box-sizing: border-box;
  padding: 5px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #868995;
  font-size: 18px;
  outline: none;
  margin-top: 27px;
  background-color: #faf8fb;
}

.service-form__label-name,
.service-form__label-phone {
  font-family: @openSans;
  font-size: 14px;
  font-style: italic;
  font-weight: normal;
  line-height: 24px;

  color: #868995;

  position: absolute;
  top: -31px;
  left: 14px;
  transition: 0.2s ease all;
  pointer-events: none;
}

.service-form__label-box {
  position: relative;
  width: 100%;
}

.service-form__label-box::before {
  position: absolute;
  bottom: 1px;
  width: 0;
  height: 2px;
  background-color: #883eca;
  content: "";
  transition-duration: 0.2s;
  transition-property: width;
}

.service-form__label-box::before {
  left: 0;
}

input:focus ~ .service-form__label-box::before {
  width: 100%;
}

.service-form__input-name:focus ~ .service-form__label-box .service-form__label-name,
.service-form__input-name:valid ~ .service-form__label-box .service-form__label-name,
.service-form__input-phone:focus ~ .service-form__label-box .service-form__label-phone,
.service-form__input-phone:valid ~ .service-form__label-box .service-form__label-phone {
  font-size: 14px;
  transform: translateY(-20px);
  color: #fb0275;
  border-color: #883eca;
}

.form-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 99.2%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;

  color: #ffffff;

  background-color: #fb0275;
  border: 2px solid transparent;
  border-radius: 25px;
  outline: none;

  margin-top: 34px;
  margin-left: 2px;

  cursor: pointer;
  transition-duration: 0.2s;
}

@media(min-width: @tablet-width) {
  .quick-polygraphy {
    background-image: url("/img/firstyle-tablet.png");
    background-size: 364px 381px;
    background-position: 103% 85.4%;
    min-height: 872px;

    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -19px;
      left: 51.2%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }

    &::after {
      position: absolute;
      content: "";
      top: -66px;
      left: 50%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }
  }

  .quick-polygraphy__title {
    width: 60%;
    height: 71px;
    font-family: @muller;
    font-size: 32px;
    line-height: 36px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0.5px;

    color: #383d52;
    margin-left: 14px;
    padding-top: 109px;

    &::before {
      top: 53px;
    }
  }

  .quick-polygraphy__description {
    width: 85%;
    letter-spacing: 0.3px;

    color: #5e606a;
    margin-top: 50px;
    margin-left: 14px;


    &--second-paragraph {
      margin-top: 23px;
    }
  }

  .form-block {
    margin-top: 43px;
  }

  .form-block__title {
    margin-left: 14px;
  }

  .form-block__subtitle {
    width: 25%;
    margin-left: 14px;
  }

  .service-form__input-name,
  .service-form__input-phone {
    width: 32.5%;
    margin-left: 14px;
  }

  input:focus ~ .service-form__label-box::before {
    width: 32.5%;
  }

  .service-form__label-box::before {
    left: 14px;
  }

  .form-button {
    width: 32.4%;
    margin-left: 16px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      top: -234px;
      left: 238px;
      width: 118px;
      height: 258px;
      background-image: url("/img/paper-plane-tablet.png");
    }
  }

  .service-form__label-name,
  .service-form__label-phone  {
    left: 27px;
  }
}

@media (min-width: @desktop-width) {
  .quick-polygraphy {
    background-image: url("/img/firstyle-desktop.png");
    background-size: 648px 544px;
    background-position: 78.2% -3%;
    min-height: 822px;
    display: flex;
    flex-direction: column;

    &::before {
      top: -18px;
      left: 50.8%;
    }

    &::after {
      top: -65px;
      left: 50.35%;
    }
  }

  .quick-polygraphy__title {
    width: 40%;
    font-family: @muller;
    font-size: 32px;
    line-height: 36px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0.5px;

    color: #383d52;
    margin-left: 20px;
    padding-top: 137px;

    &::before {
      left: -90px;
      top: 80px;
    }
  }

  .quick-polygraphy__description {
    width: 50%;
    letter-spacing: 0.3px;

    color: #5e606a;
    margin-top: 50px;
    margin-left: 14px;


    &--second-paragraph {
      margin-top: 23px;
    }
  }

  form {
    display: flex;
  }

  .form-block {
    width: 100%;
    margin-top: 85px;
  }

  .form-block__title {
    margin-left: 22px;
  }

  .form-block__subtitle {
    width: 100%;
    margin-left: 22px;
  }

  .service-form__input-name,
  .service-form__input-phone {
    width: 77%;
    margin-left: 22px;
    margin-top: 37px;
    margin-right: 50px;
  }

  .service-form__input-phone {
    margin-right: 0;
  }

  input:focus ~ .service-form__label-box::before {
    width: 77%;
  }

  .service-form__label-name,
  .service-form__label-phone {
    left: 37px;
  }

  .service-form__label-box::before {
    left: 22px;
  }

  .form-button {
    width: 16.4%;
    margin-top: 24px;
    margin-left: 40px;

    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
      border: 2px solid #fb0275;
    }

    &:active {
      opacity: 0.5;
    }

    /*&::after {
      position: absolute;
      content: "";
      top: -259px;
      left: 585px;
      //background-image: url("/img/paper-plane-desktop.png");
      background-repeat: no-repeat;
    }*/
  }
}
