footer {
  background-color: #65269d;
  min-height: 164px;
  margin-top: -10px;
}

.footer__politik {
  font-family: @openSans;
  font-size: 13px;
  font-style: normal;
  font-weight: normal;

  color: #ffffff;

  padding-top: 52px;
}

.footer__copyrigth {
  font-family: @openSans;
  font-size: 13px;
  font-style: normal;
  font-weight: normal;

  color: #ffffff;
  padding-top: 36px;
  padding-bottom: 50px;
}

.footer__design {
  display: none;
}

@media(min-width: @tablet-width) {
  footer {
    background-color: #65269d;
    min-height: 62px;
  }

  .footer__politik{
    padding: 0;
    padding-top: 25px;
    margin-left: 48%;
    order: 2;
  }

  .footer__copyrigth {
    padding: 0;
    padding-top: 27px;
    order: 1;
  }
}


@media(min-width: @desktop-width) {
  footer {
    min-height: 49px;
  }

  .footer__politik{
    padding: 0;
    padding-top: 20px;
    margin: 0;
    order: 2;
  }

  .footer__copyrigth {
    padding: 0;
    padding-top: 20px;
    order: 1;
    margin-left: 25px;
  }

  .footer__design {
    display: block;
    font-family: @openSans;
    font-size: 13px;
    font-style: normal;
    font-weight: normal;

    color: #ffffff;
    padding-top: 20px;
    order: 3;
    margin-right: 9px;

  }
}
