.menu-list,
.contact-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

.menu-list__item {
  background-color: #ffffff;
}

.menu-list__item a {
  display: block;
  font-family: @openSans;
  font-size: 13px;
  font-weight: 600;
  font-style: normal;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  padding: 20px 0;
  color: #000000;
}

@media (min-width: @desktop-width) {
  .menu-list,
  .contact-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    padding-top: 59px;
  }

  .menu-list {
    width: 66%;
    flex-wrap: wrap;
    margin-left: 13.8%;
  }

  .menu-list__item {
    margin-right: 57px;
    margin-top: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  .contact-list {
    width: 45%;
    margin-left: 40px;
    flex-wrap: wrap;
  }

  .menu-list__item a {
    padding: 0;
    color: #ffffff;
  }

  .menu-list__item a:hover,
  .menu-list__item a:focus {
    color: #fb0275;
  }

  .menu-list__item a:active {
    opacity: 0.5;
  }
}

