.work-stages-list {
  .list-reset();
  margin-top: 203px;
  padding-bottom: 50px;
}

.work-stages-list__item {
  width: 156px;
  font-family: @openSans;
  font-size: 13px;
  font-style: normal;
  font-weight: normal;

  text-align: center;

  color: #5e606a;
  position: relative;

  &--order {
    margin-left: 24%;
  }

  &--order::before {
    position: absolute;
    content: "";
    top: -120px;
    left: 15%;
    width: 106px;
    height: 106px;
    background-image: url("/img/ebook.png");
  }

  &--order::after {
    position: absolute;
    content: "";
    top: -39px;
    left: 81.5%;
    width: 58px;
    height: 113px;
    background-image: url("/img/first-step.png");
  }

  &--maket {
    width: 100px;
    line-height: 18px;
    margin-left: 64%;
    margin-top: 165px;
  }

  &--maket::before {
    position: absolute;
    content: "";
    top: -117px;
    left: -4%;
    width: 106px;
    height: 106px;
    background-image: url("/img/tablet.png");
  }

  &--maket::after {
    position: absolute;
    content: "";
    top: -50px;
    left: -83%;
    width: 74px;
    height: 42px;
    background-image: url("/img/second-step.png");
  }

  &--printing {
    width: 120px;
    line-height: 18px;
    margin-left: 1%;
    margin-top: 40px;
  }

  &--printing::before {
    position: absolute;
    content: "";
    top: -117px;
    left: 7%;
    width: 106px;
    height: 106px;
    background-image: url("/img/writer.png");
  }

  &--printing::after {
    position: absolute;
    content: "";
    top: -20px;
    left: 97.5%;
    width: 77px;
    height: 144px;
    background-image: url("/img/third-step.png");
  }

  &--delivery {
    width: 170px;
    line-height: 18px;
    margin-left: 20%;
    margin-top: 167px;
  }

  &--delivery::before {
    position: absolute;
    content: "";
    top: -117px;
    left: 20%;
    width: 106px;
    height: 106px;
    background-image: url("/img/delivery.png");
  }
}

@media (min-width: @tablet-width) {
  .work-stages-list {
    margin-top: 170px;
    padding-bottom: 137px;
  }

  .work-stages-list__item {
    &--order::before {
      top: -122px;
      left: 18.5%;
    }

    &--order::after {
      position: absolute;
      content: "";
      top: -65px;
      left: 89%;
      width: 277px;
      height: 77px;
      background-image: url("/img/first-step-tablet.png");
    }

    &--maket::before {
      top: -117px;
      left: 50px;
    }

    &--maket::after {
      position: absolute;
      content: "";
      top: -43px;
      left: -209%;
      width: 478px;
      height: 66px;
      background-image: url("/img/second-step-tablet.png");
      background-repeat: no-repeat;
    }

    &--printing::before {
      top: -120px;
      left: 25px;
    }

    &--printing::after {
      position: absolute;
      content: "";
      top: -23px;
      left: 77%;
      width: 279px;
      height: 55px;
      background-image: url("/img/third-step-tablet.png");
      background-repeat: no-repeat;
    }

    &--delivery::before {
      top: -120px;
      left: 33px;
    }

    &--maket {
      width: 200px;
      line-height: 18px;
      margin-left: 74%;
      margin-top: 62px;
    }

    &--printing {
      width: 150px;
      line-height: 18px;
      margin-left: 1.5%;
      margin-top: 50px;
    }

    &--delivery {
      width: 170px;
      line-height: 18px;
      margin-left: 51%;
      margin-top: 65px;
    }
  }
}

@media (min-width: @desktop-width) {
  .work-stages-list {
    width: 50%;
    order: 1;
    margin-top: 236px;
    padding-bottom: 146px;
  }

  /*Элементы списка*/
  .work-stages-list__item {
    &--order {
      margin-left: 33%;
    }

    &--maket {
      width: 200px;
      line-height: 18px;
      margin-left: 65%;
      margin-top: 106px;
    }

    &--printing {
      width: 150px;
      line-height: 18px;
      margin-left: 4.1%;
      margin-top: 5px;
    }

    &--delivery {
      width: 170px;
      line-height: 18px;
      margin-left: 39%;
      margin-top: 97px;
    }

    &--order::before {
      top: -121px;
      left: 20px;
    }

    &--maket::before {
      top: -117px;
      left: 43px;
    }

    &--printing::before {
      top: -118px;
      left: 20px;
    }

    &--delivery::before {
      top: -119px;
      left: 25px;
    }

    &--order::after {
      top: -55px;
      left: 82%;
      width: 125px;
      height: 77px;
      background-image: url("/img/first-step-desktop.png");
    }

    &--maket::after {
      top: -81px;
      left: -125%;
      width: 301px;
      height: 59px;
      background-image: url("/img/second-step-desktop.png");
      background-repeat: no-repeat;
    }

    &--printing::after {
      top: -29px;
      left: 77%;
      width: 134px;
      height: 66px;
      background-image: url("/img/third-step-desktop.png");
      background-repeat: no-repeat;
    }
  }
}
