.interier-printing {
  min-height: 685px;
  padding-bottom: 63px;

  background-image: url("/img/divan.png");
  background-size: 281px 355px;
  background-position: right 86.5%;
  background-repeat: no-repeat;
}

.interier-printing__title {
  width: 90%;
  height: 84px;
  font-family: @muller;
  font-size: 24px;
  line-height: 28px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.5px;

  color: #383d52;
  padding-top: 70px;
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    left: 15px;
    top: 24px;
    width: 145px;
    height: 171px;
    background-image: url("/img/number-two.png");
    z-index: -1;
  }
}

.interier-printing__description {
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.5px;

  color: #5e606a;
  margin-top: 40px;
}

.interier-printing__button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 99%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;

  color: #ffffff;

  background-color: #fb0275;
  border: 2px solid transparent;
  border-radius: 25px;
  outline: none;

  margin-top: 66px;

  cursor: pointer;
  transition-duration: 0.2s;
}

@media(min-width: @tablet-width) {
  .interier-printing {
    min-height: 745px;

    background-image: url("/img/divan-tablet.png");
    background-size: 536px 640px;
    background-position: 105% 65.5%;
    background-repeat: no-repeat;

    padding-bottom: 70px;
  }

  .interier-printing__title {
    width: 60%;
    height: 71px;
    font-size: 32px;
    line-height: 36px;
    margin-left: 15px;

    &::before {
      top: 11px;
    }
  }

  .interier-printing__description {
    width: 80%;
    letter-spacing: 0.7px;
    margin-top: 60px;
    margin-left: 15px;
  }

  .interier-printing__button {
    width: 32.6%;
    height: 48px;

    font-family: @openSans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;

    margin-top: 46px;
    margin-left: 13px;
  }
}

@media(min-width: @desktop-width) {
  .interier-printing {
    min-height: 973px;

    background-image: url("/img/divan-desktop.png");
    background-size: 1276px 813px;
    background-position: 19% 56%;
    background-repeat: no-repeat;
  }

  .interier-printing__title {
    width: 40%;
    font-size: 32px;
    line-height: 38px;
    margin-left: 52%;
    padding-top: 126px;

    &::before {
      top: 70px;
      left: -90px;
      width: 145px;
      height: 171px;
    }
  }

  .interier-printing__description {
    width: 80%;
    letter-spacing: 0.7px;
    margin-top: 50px;
    margin-left: 52%;
  }

  .interier-printing__button {
    width: 20.6%;
    height: 48px;

    font-family: @openSans;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;

    margin-top: 60px;
    margin-left: 52%;

    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
      border: 2px solid #fb0275;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
