.wrapper {
  margin: 0 auto;
  padding: 0 15px;

  &--header {
    padding: 0;
  }

  &--footer {
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media (min-width: @tablet-width) {
  .wrapper {
      &--printing-house {
      display: flex;
    }

    &--footer {
      flex-direction: row;
    }
  }
}

@media (min-width: @desktop-width) {
  .wrapper {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 20px;

    display: flex;

    &--services {
      display: flex;
      flex-wrap: wrap;
      position: relative;
    }

    &--footer {
      justify-content: space-between;
    }
  }
}
