.header-logo {
  z-index: 100;
}

.header {
  z-index: 100;
}

.header-logo__image {
  margin-left: 15px;
  padding-top: 20px;
  z-index: 100;
}

.header-active {
  background-image: linear-gradient(to right, #8e3eda, #6f319b);
}

@media (min-width: @tablet-width) {
  .header-logo__image {
    margin-left: 32px;
    padding-top: 28px;
  }
}

@media (min-width: @desktop-width) {
  .header-logo__image {
    margin-left: 25px;
    padding-top: 70px;
    z-index: 100;
  }
}


