.responses__title {
    display: none;
}

.responses-list {
  .list-reset();
}

.responses-list__response {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 10px 0 rgba(81, 8, 86, 0.2);
  border-radius: 5px;
  margin-top: 95px;
  align-items: center;
}

.reviews-list__response-avatar {
  margin-left: auto;
  margin-right: auto;
  padding-top: 60px;
}

.reviews-list__response-text {
  width: 82%;
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: 0.1px;

  color: #5e606a;

  padding-top: 40px;
  margin-left: 7%;
}

.reviews-list__response-author {
  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.2px;
  color: #383d52;
  padding-top: 33px;
  margin-left: 7%;
}

.reviews-list__response-social-link {
  font-family: @openSans;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #3594dd;
  text-decoration: none;
  margin-left: 7%;
  padding-bottom: 45px;
}

.response-control-block {
  display: flex;
  justify-content: center;

  padding-top: 93px;
}

.response-control {
  background-image: url('/img/arrow-slider.png');
  background-repeat: no-repeat;

  width: 91px;
  height: 14px;
  border: none;
  outline: none;
  background-color: transparent;

  &--left {
    transform: rotate(180deg);
    margin-right: 42px;
  }
}

@media (min-width: @tablet-width) {
  .responses__title {
    display: block;
    font-family: @muller;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    color: #7f38bd;

    margin-left: 47px;
    padding-top: 71px;

    position: relative;

    &::before {
      position: absolute;
      content: "3.";
      left: -32px;
      top: 70px;
    }
}

  .responses-list__response {
    width: 97%;
    flex-direction: row;
    margin-top: 60px;
    margin-left: 17px;
  }

  .reviews-list__response-avatar {
    margin-left: 45.5%;
    padding-top: 60px;
    padding-bottom: 70px;
  }

  .reviews-list__response-description {
    margin-top: -45px;
    margin-left: 76px;
  }

  .reviews-list__response-author {
    padding-top: 8px;
    margin-left: 7%;
  }

  .response-control-block {
    justify-content: left;
    margin-left: 17px;
    padding-top: 37px;
  }
}

@media (min-width: @desktop-width) {
  .responses__title {
    margin-left: 53px;
    padding-top: 130px;

    &::before {
      left: -32px;
      top: 132px;
    }
  }

  .responses-list__response {
    width: 72%;
    flex-direction: row;
    margin-top: 80px;
    margin-left: 172px;
  }

  .reviews-list__response-avatar {
    margin-left: 55.5%;
    padding-top: 60px;
  }

  .reviews-list__response-description {
    margin-top: 15px;
    margin-left: 76px;
  }

  .reviews-list__response-text {
    width: 81%;
    margin-top: -57px;
  }

  .reviews-list__response-social-link {
    font-size: 14px;
  }

  .response-control-block {
    margin-left: 168px;
  }

  .response-control {
    cursor: pointer;
  }
}
