.services {
  background-color: #faf8fb;
  padding-bottom: 72px;
}

.service-image {
  display: block;
  width: 100%;
}

.services__title {
    font-family: @muller;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    color: #7f38bd;

    margin-left: 30px;
    padding-top: 67px;
    position: relative;

    &::before {
      position: absolute;
      content: "2.";
      left: -28px;
      top: 66px;
    }
}

.services__subtitle {
  width: 90%;
  height: 84px;
  font-family: @muller;
  font-size: 24px;
  line-height: 28px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.5px;

  color: #383d52;
  margin-top: 39px;
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    left: 15px;
    top: -33px;
    width: 127px;
    height: 168px;
    background-image: url("/img/number-one.png");
    z-index: -1;
  }
}

.services-list {
  margin: 0;
  padding: 0;

  margin-top: 45px;
  margin-left: 15px;
}

.services-list__item {
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.5px;

  color: #5e606a;
}

@media (min-width: @tablet-width) {
  .services {
    padding-bottom: 68px;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -22px;
      left: 50.8%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }

    &::after {
      position: absolute;
      content: "";
      top: -69px;
      left: 49.6%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }
  }

  .services__title {
    margin-left: 45px;
    padding-top: 75px;

    &::before {
      left: -30px;
      top: 76px;
    }
  }

  .services__subtitle {
    width: 90%;
    height: 77px;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.5px;

    margin-left: 15px;
    margin-top: 60px;

    &::before {
      top: -53px;
    }
  }

  .services-list {
    margin-top: 57px;
    margin-left: 30px;
  }
}

@media (min-width: @desktop-width) {
  .services {
    padding-bottom: 121px;

    &::before {
      top: -18px;
      left: 50.8%;
    }

    &::after {
      top: -65px;
      left: 50.35%;
    }
  }

  .test {
    width: 40%;
    position: relative;
    margin-right: 9%;
  }

  .services__title {
    padding-top: 95px;
    margin-left: 57px;

    &::before {
      left: -32px;
      top: 95px;
    }
  }

  .services__subtitle {
    width: 500px;
    font-family: @muller;
    font-size: 32px;
    line-height: 38px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 1px;

    color: #383d52;
    margin-left: 25px;
    margin-top: 76px;

    &::before {
      top: -53px;
      left: -90px;
    }
  }

  .services-list {
    margin-top: 49px;
    margin-left: 43px;
  }
}
