.podlojka {
  background-color: rgba(0, 0, 0, 0.4);
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
  left: 0;
  display: none;
}

.callback-popup {
  width: 290px;
  min-height: 369px;
  background-color: #883eca;
  left: 5%;
  top: 15%;
  position: fixed;
  border-radius: 10px;
  z-index: 1000;
}

.podlojka-open {
  display: block;
}

.callback-popup__title {
  font-family: @openSans;
  font-size: 16px;
  font-style: italic;
  font-weight: normal;
  line-height: 30px;

  color: #ffffff;
  margin-top: 37px;
  margin-left: 60px;
}

.callback-popup__subtitle {
  width: 68%;
  font-family: @openSans;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.55px;

  color: #ffffff;

  margin-left: 60px;
}

.callback-popup-form {
  display: flex;
  flex-direction: column;

  //margin-top: 9.5px;
  position: relative;
}

.callback-popup-form {
  display: flex;
  flex-direction: column;

  //margin-top: 9.5px;
  position: relative;
}


.callback-popup-form__input-name,
.callback-popup-form__input-phone {
  box-sizing: border-box;
  padding: 5px;
  width: 66.2%;
  border: none;
  border-bottom: 1px solid #ffffff;
  font-size: 18px;
  outline: none;

  background-color: #883eca;
  margin-top: 36px;
  margin-left: 62px;

  color: #ffffff;
}

.callback-popup-form__label-name,
.callback-popup-form__label-phone {
  font-family: @openSans;
  font-size: 14px;
  font-style: italic;
  font-weight: normal;
  line-height: 24px;

  color: #ffffff;

  position: absolute;
  top: -31px;
  left: 75px;
  transition: 0.2s ease all;
  pointer-events: none;
}

.callback-popup-form__label-box {
  position: relative;
  width: 100%;
}

.callback-popup-form__label-box::before {
  position: absolute;
  bottom: 1px;
  width: 0;
  height: 2px;
  background-color: #ffffff;
  content: "";
  transition-duration: 0.2s;
  transition-property: width;
}

.callback-popup-form__label-box::before {
  left: 62px;
}

input:focus ~ .callback-popup-form__label-box::before {
  width: 66.2%;
}

.callback-popup-form__input-name:focus ~ .callback-popup-form__label-box .callback-popup-form__label-name,
.callback-popup-form__input-name:valid ~ .callback-popup-form__label-box .callback-popup-form__label-name,
.callback-popup-form__input-phone:focus ~ .callback-popup-form__label-box .callback-popup-form__label-phone,
.callback-popup-form__input-phone:valid ~ .callback-popup-form__label-box .callback-popup-form__label-phone {
  font-size: 14px;
  transform: translateY(-20px);
  color: #fb0275;
  border-color: #883eca;
}

.callback-popup-form__button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 66.5%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;

  color: #ffffff;

  background-color: #fb0275;
  border: none;
  border-radius: 25px;
  outline: none;

  margin-top: 34px;
  margin-left: auto;
  margin-right: auto;

  cursor: pointer;
  transition-duration: 0.2s;

  &:active {
    opacity: 0.5;
  }
}

.modal-close {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-color: transparent;

  border: none;
  outline: none;

  top: 20px;
  right: 20px;
  cursor: pointer;

  &::before {
    position: absolute;
    content: "";
    width: 21px;
    height: 2px;
    background-color: #ffffff;
    top: 6px;
    left: -2px;
    transform: rotate(45deg);
  }

  &::after {
    position: absolute;
    content: "";
    width: 21px;
    height: 2px;
    background-color: #ffffff;
    top: 6px;
    left: -2px;
    transform: rotate(-45deg);
  }

  &:active {
    opacity: 0.5;
  }
}

@media(min-width: @tablet-width) {
  .callback-popup {
    width: 363px;
    min-height: 369px;
    background-color: #883eca;
    left: 30%;
    top: 15%;
    position: fixed;
    border-radius: 10px;
    z-index: 1000;
  }
}

@media(min-width: @desktop-width) {
  .callback-popup {
    width: 363px;
    left: 40%;
    top: 15%;
  }

  .callback-popup-form__button {
    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
    }

    &:active {
      opacity: 0.5;
    }
  }

  .modal-close {
    &:hover::before,
    &:hover::after,
    &:focus::before,
    &:focus::before {
      background-color: #fb0275;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
