.about-us__title {
  font-family: @muller;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;

  color: #7f38bd;

  margin-left: 28px;
  padding-top: 35px;

  position: relative;

  &::before {
    position: absolute;
    content: "1.";
    left: -31px;
    top: 34px;
  }
}

.about-us__subtitle {
  width: 90%;
  font-family: @muller;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0.5px;

  color: #383d52;

  margin-top: 22px;
}

.about-us__description {
  width: 93%;
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: -0.5px;

  color: #5e606a;

  &--first-paragraph {
    margin-top: 34px;
  }

  &--second-paragraph {
    width: 98%;
    margin-top: 23px;
  }

  &--third-paragraph {
    width: 98%;
    margin-top: 23px;
  }
}

@media (min-width: @tablet-width) {
  .about-us__title {
    margin-left: 42px;
    padding-top: 46px;

    &::before {
      content: "1.";
      left: -30px;
      top: 45px;
    }
   }

   .about-us__subtitle {
    width: 100%;
    font-family: @muller;
    font-size: 32px;
    letter-spacing: 1px;

    margin-top: 46px;
    margin-left: 12px;
  }

  .about-us__description {
    width: 96%;
    letter-spacing: 0.4px;
    margin-left: 14px;

    &--first-paragraph {
      margin-top: 29px;
    }

    &--second-paragraph,
    &--third-paragraph {
      width: 98%;
      margin-top: 24px;
    }
  }
}

@media (min-width: @desktop-width) {
  .about-us__info-block {
    width: 50%;
    order: 2;
  }

  .about-us__title {
    margin-left: 87px;
    margin-top: 57px;
  }

  .about-us__subtitle {
    width: 65%;
    margin-top: 67px;
    margin-left: 60px;
  }

  .about-us__description {
    width: 82%;
    letter-spacing: 0.4px;
    margin-left: 60px;

    &--first-paragraph {
      margin-top: 50px;
    }

    &--second-paragraph,
    &--third-paragraph {
      width: 88.6%;
      margin-top: 24px;
    }
  }
}
