.contact {
  position: relative;
}

.contact-block {
  width: 290px;
  min-height: 265px;

  position: absolute;
  top: 66px;

  background-color: #ffffff;
  border-radius: 10px;
}

.contact__title {
  font-family: @muller;
  font-size: 30px;
  font-style: normal;
  font-weight: 600;

  color: #7f38bd;

  margin-left: 70px;
  margin-top: 40px;

  position: relative;

  &::before {
    position: absolute;
    content: "5.";
    left: -31px;
    top: 0;
  }
}

.contact-list2 {
  .list-reset();
  margin-left: 66px;
  margin-top: 22px;
}

.contact-list2__item {
  width: 200px;
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  color: #5e606a;
  margin-bottom: 24px;
  letter-spacing: 0.2px;

  position: relative;

  &--adress::before {
    position: absolute;
    content: "";
    width: 11px;
    height: 17px;
    top: 4px;
    left: -27px;
    background-image: url("/img/map-pin.png");
  }

  &--email::before {
    position: absolute;
    content: "";
    width: 23px;
    height: 17px;
    top: 0;
    left: -35px;
    background-image: url("/img/icon-email.svg");
    background-repeat: no-repeat;
}

  &--phone::before {
    position: absolute;
    content: "";
    width: 23px;
    height: 17px;
    top: -6px;
    left: -34px;
    background-image: url("/img/icon-phone.svg");
    background-repeat: no-repeat;
  }
}

.contact-list2 a {
  text-decoration: none;
  color: #5e606a;
}

@media(min-width: @tablet-width) {
  .contact__map-iframe {
    height: 470px;
  }

  .contact-block {
    width: 385px;
    min-height: 322px;
    top: 63px;
    left: 30px;
  }

  .contact__title {
    font-family: @muller;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;

    color: #7f38bd;

    margin-left: 89px;
    margin-top: 60px;
  }

  .contact-list2 {
    .list-reset();
    margin-left: 85px;
    margin-top: 55px;
  }

  .contact-list2 li {
    width: 250px;
    margin-bottom: 23px;
  }
}

@media(min-width: @desktop-width) {
  .contact {
    position: relative;
    &::before {
      position: absolute;
      content: "";
      top: -15px;
      left: 50.8%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }

    &::after {
      position: absolute;
      content: "";
      top: -62px;
      left: 50.35%;
      width: 2px;
      height: 83px;
      background-color: #883eca;
    }
  }

  .contact__map-iframe {
    height: 653px;
  }

  .contact-block {
    width: 458px;
    min-height: 382px;
    top: 120px;
    left: 20.5%;
  }

  .contact__title {
    font-family: @muller;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;

    color: #7f38bd;

    margin-left: 119px;
    margin-top: 82px;
  }

  .contact-list2 {
    .list-reset();
    margin-left: 117px;
    margin-top: 55px;
  }
}
