.consultation {
  display: flex;
  flex-direction: column;

  width: 100%;
  min-height: 181px;

  background-color: #883eca;
  border-radius: 10px;
  box-shadow: 0px 23px 32px 0px rgba(81, 8, 86, 0.33);

  margin-top: 43px;
}

.consultation__title {
  font-family: @openSans;
  font-size: 13px;
  font-style: italic;
  font-weight: normal;
  line-height: 24px;
  color: #ffffff;

  margin-left: 25px;
  margin-top: 25px;
}

.consultation__subtitle {
  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.8px;
  color: #ffffff;

  margin-left: 25px;
}

.consultation__button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 84%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;

  color: #ffffff;


  background-color: #fb0275;
  border: none;
  border-radius: 25px;
  outline: none;

  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;

  cursor: pointer;
  transition-duration: 0.2s;
}

@media (min-width: @tablet-width) {
  .consultation {
    width: 97.5%;
    min-height: 117px;

    flex-direction: row;
    align-items: center;
    justify-content: space-around;

    margin-top: 49px;
    margin-left: 15px;
  }

  .consultation__title {
    margin-left: 7px;
    margin-top: 0;
  }

  .consultation__subtitle {
    margin-left: -35px;
  }

  .consultation__button {
    width: 34.5%;
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .consultation {
    width: 473px;
    display: block;

    position: absolute;
    top: 499px;
    left: 28px;
  }

  .consultation__title {
    margin-left: 35px;

    position: absolute;
    top: 34px;
  }

  .consultation__subtitle {
    position: relative;

    margin-left: 35px;
    margin-top: 55px;
  }

  .consultation__button {
    width: 36%;

    margin: 0;
    margin-left: 265px;
    margin-top: -45px;

    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
    }

    &:active {
      opacity: 0.5;
    }
  }
}

