.banner-list {
  .list-reset;
  position: relative;
  margin-top: 44px;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    width: 96%;
    min-height: 187px;
    top: -5px;
    left: 2%;
    background-color: #b9b2bb;
    border-radius: 10px;
    z-index: -1;
  }

  &::after {
    position: absolute;
    content: "";
    width: 92.5%;
    min-height: 187px;
    top: -8px;
    left: 4%;
    background-color: #e1dbe7;
    border-radius: 10px;
    z-index: -2;
  }
}

.service-image {
  box-shadow: 0 20px 46px 0 rgba(81, 8, 86, 0.35);
}

.banner-list__adress {
  font-family: @openSans;
  font-size: 13px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;

  color: #5e606a;
  margin-top: 12px;
}

.banner-control-block {
  display: flex;
  justify-content: center;
  padding-top: 26px;
}

.banner-control {
  background-image: url('/img/arrow-slider.png');
  background-repeat: no-repeat;

  width: 91px;
  height: 14px;
  border: none;
  outline: none;
  background-color: transparent;

  &--left {
    transform: rotate(180deg);
    margin-right: 42px;
  }
}

@media (min-width: @tablet-width) {
  .banner-list {
    .list-reset;
    margin-top: 66px;
    margin-left: 15px;

    &::before {
      width: 95%;
      top: -9px;
      left: 2.2%;
    }

    &::after {
      width: 90%;
      top: -20px;
      left: 5%;
    }
  }

  .banner-list__adress {
    text-align: right;
    margin-top: 36px;
    margin-right: 10px;
  }

  .banner-control-block {
    display: flex;
    justify-content: left;

    padding-top: 0;
    margin-top: -18px;
    margin-left: 15px;
  }
}

@media (min-width: @desktop-width) {
  .banner-list {
    .list-reset;
    margin-top: 204px;
  }

  .banner-list__adress {
    margin-right: 125px;
  }

  .banner-control-block {
    margin-left: 595px;
  }
}
