.header-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 200px;
  height: 48px;

  font-family: @openSans;
  font-size: 11px;
  font-weight: 800;
  font-style: normal;
  text-decoration: none;
  text-transform: uppercase;
  color: #fb0275;
  border: 2px solid #fb0275;
  border-radius: 25px;

  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}


@media (min-width: @desktop-width) {
  .header-button {
    width: 110%;
    height: 30px;

    font-family: @openSans;
    font-size: 11px;
    font-weight: 800;
    font-style: normal;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    border: 2px solid #ffffff;
    border-radius: 25px;

    margin: 0;
    margin-left: 20px;
    padding-top: 2px;

    &:hover,
    &:focus {
      background-color: #fb0275;
      border-color: #fb0275;
      transition-duration: 0.2s;
    }

    &:active {
      opacity: 0.5;
    }
  }
}

