.header-phone {
  display: block;
  font-family: @openSans;
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  padding: 20px;
}

@media (min-width: @desktop-width) {
  .header-phone {
    width: 175px;
    font-family: @openSans;
    font-size: 16px;
    font-weight: 700;
    font-style: normal;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #ffffff;
    padding: 0;
    padding-top: 5px;

    &:hover,
    &:focus {
      color: #fb0275;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
