.main-navigation {
  background-color: #ffffff;
  position: relative;
  margin-top: 10px;
}

.main-navigation__toggle {
  position: absolute;
  content: "";
  top: -30px;
  right: 3%;
  border: none;
  background-color: transparent;
  height: 20px;
  z-index: 10;
}

.main-navigation__toggle span {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #ffffff;
  transition-duration: 0.7s;
}

.main-navigation__toggle span::before,
.main-navigation__toggle span::after {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #ffffff;
  content: "";
  transition-timing-function: ease-in-out;
  transition-duration: 0.7s;
}

.main-navigation__toggle span::before {
  transform: translate(0, 7px);
}

.main-navigation__toggle span::after {
  transform: translate(0, -9px);
}

.main-navigation__toggle--opened span {
  transform: rotate(180deg);
  background-color: transparent;
}

.main-navigation__toggle--opened span::before {
  transform: translate(0, 0) rotate(45deg);
  background-color: #ffffff;
}

.main-navigation__toggle--opened span::after {
  transform: translate(0, -2px) rotate(-45deg);
  background-color: #ffffff;
}

.main-navigation__toggle--opened .menu-list,
.main-navigation__toggle--opened .contact-list {
  display: flex;
  flex-direction: column;
  transition-timing-function: ease-in-out;
  transition-duration: 0.7s;
}
/*
.main-navigation__toggle--opened ~ header {
  background-color: red;
}*/

@media (min-width: @tablet-width) {
  .main-navigation__toggle {
    right: 1.5%;
  }

  .main-navigation__toggle span {
    width: 26px;
  }

  .main-navigation__toggle span::before,
  .main-navigation__toggle span::after {
    width: 26px;
  }
}


@media (min-width: @desktop-width) {
  .main-navigation {
    width: 100%;
    display: flex;
    align-items: center;
    background-color: transparent;
    margin: 0;
    padding-right: 2%;
    z-index: 100;
  }

  .menu-list__item {
    background-color: transparent;
  }

  .main-navigation__toggle--opened .menu-list,
  .main-navigation__toggle--opened .contact-list {
    display: flex;
    flex-direction: row;
    transition-timing-function: ease-in-out;
    transition-duration: 0.7s;
  }

  .main-navigation__toggle {
    display: none;
  }
}
