.faq {
  padding-bottom: 85px;
}

.faq__title {
  font-family: @muller;
  font-size: 30px;
  line-height: 30px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: -1px;

  color: #7f38bd;
  padding-top: 60px;
  margin-left: 31px;

  position: relative;

  &::before {
    position: absolute;
    content: "4.";
    left: -31px;
    top: 60px;
  }
}

.faq-list {
  .list-reset();
  margin-top: 68px;
  margin-bottom: 0;

  &--second-list {
    margin-top: 3px;
  }
}

.faq-list__item-question {
  width: 95%;
  font-family: @openSans;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: -0.6px;

  color: #383d52;
  padding-bottom: 16px;
}

.faq-list__item-answer {
  width: 100%;
  font-family: @openSans;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: 0.5px;

  color: #5e606a;
  padding-bottom: 33px;
}

.faq-form-block {
  width: 100%;
  min-height: 392px;
  background-color: #883eca;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 23px 32px 0px rgba(81, 8, 86, 0.33);

  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: -8px;
    right: -15px;
    width: 68px;
    height: 73px;
    background-image: url("/img/question-mark.png");
  }
}

.faq-form__title {
  font-family: @openSans;
  font-size: 16px;
  font-style: italic;
  font-weight: normal;
  line-height: 30px;

  padding-top: 37px;
  margin-left: 23px;

  color: #ffffff;
}

.faq-form__subtitle {
  width: 60%;
  font-family: @openSans;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.2px;
  margin-left: 23px;

  color: #ffffff;
}

.faq-form__input-name,
.faq-form__input-phone {
  width: 83.6%;

  font-size: 18px;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #ffffff;
  outline: none;
  background-color: transparent;
  color: #ffffff;
  padding: 5px;
  margin-top: 28.5px;
  margin-left: 24px;
}

.faq-form__label-name,
.faq-form__label-phone {
  font-family: @openSans;
  font-size: 14px;
  font-style: italic;
  font-weight: normal;
  line-height: 24px;

  color: #ffffff;

  position: absolute;
  top: -31px;
  left: 37px;
  transition: 0.2s ease all;
  pointer-events: none;
}

.faq-form__label-box {
  position: relative;
  width: 100%;
}

.faq-form__label-box::before {
  position: absolute;
  content: "";
  bottom: 1px;
  width: 0;
  height: 2px;
  background-color: #ffffff;
  transition-duration: 0.2s;
  transition-property: width;
}

.faq-form__label-box::before {
  left: 24px;
}

input:focus ~ .faq-form__label-box::before {
  width: 83.5%;
}

.faq-form__input-name:focus ~ .faq-form__label-box .faq-form__label-name,
.faq-form__input-name:valid ~ .faq-form__label-box .faq-form__label-name,
.faq-form__input-phone:focus ~ .faq-form__label-box .faq-form__label-phone,
.faq-form__input-phone:valid ~ .faq-form__label-box .faq-form__label-phone {
  font-size: 14px;
  transform: translateY(-20px);
  color: #fb0275;
  border-color: #ffffff;
}

.faq-form__button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 84%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;

  color: #ffffff;

  background-color: #fb0275;
  border: none;
  border-radius: 25px;
  outline: none;

  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;

  cursor: pointer;
  transition-duration: 0.2s;
}

@keyframes rotate{
	from{transform: rotate(0deg);}
	to{transform: rotate(360deg)}
}

@keyframes alterrotate{
	from{transform: rotate(0deg);}
	to{transform: rotate(-360deg)}
}

@media(min-width: @tablet-width) {
  .faq {
    padding-bottom: 102px;
  }

  .faq__title {
    font-family: @muller;
    font-size: 30px;
    line-height: 30px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: -1px;
    color: #7f38bd;

    padding-top: 80px;
    margin-left: 45px;

    &::before {
      top: 80px;
    }
  }

  .faq-list {
    .list-reset();
    margin-top: 52px;
    margin-left: 15px;
    margin-bottom: 0;

    &--second-list {
      margin-top: 0;
      margin-bottom: 10px;
    }
  }

  .faq-list__item-question {
    width: 95%;
    font-family: @openSans;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.6px;
    color: #383d52;

    padding-bottom: 21px;
  }

  .faq-list__item-answer {
    width: 100%;
    font-family: @openSans;
    font-size: 14px;
    font-style: normal;
    font-weight: normal;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #5e606a;

    padding-bottom: 30px;
  }

  .faq-form__title {
    margin-left: 35px;
  }

  .faq-form__subtitle {
    margin-left: 35px;
  }

  .faq-form-block {
    width: 97.7%;
    min-height: 220px;
    background-image: url("/img/background-faq-form-tablet.png");
    background-size: cover;
    border-radius: 10px;

    margin-top: 18px;
    margin-left: 14px;

    &::before {
      position: absolute;
      content: "";
      top: 18px;
      right: 78px;
      width: 30px;
      height: 50px;
      background-image: url("/img/question-mark-tablet.png");
    }

    &::after {
      position: absolute;
      content: "";
      top: -10px;
      right: 36px;
      width: 112px;
      height: 112px;
      background-image: url("/img/small-ellips-tablet.png");
      animation:rotate 10s linear infinite;
    }
  }

  .faq-form {
    width: 95%;
    display: flex;

    margin-top: 8px;
    margin-left: 13px;

    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -136px;
      right: -8px;
      width: 154px;
      height: 154px;
      background-image: url("/img/medium-ellips-tablet.png");
      animation:rotate 10s linear infinite;
    }
  }

  .faq-form__input-name,
  .faq-form__input-phone {
    width: 73%;
    border-bottom: 1px solid #ffffff;
    background-color: transparent;

    margin-top: 28.5px;
    margin-left: 24px;
  }

  .faq-form__input-phone {
    margin-left: 9px;
  }

  .faq-form__label-phone {
    left: 17px;
  }

  .faq-form__label-box--phone::before {
    left: 9px;
  }

  input:focus ~ .faq-form__label-box::before {
    width: 73%;
  }

  .faq-form__button {
    width: 28.5%;

    margin-top: 16px;
    margin-right: 8px;
  }
}


@media(min-width: @desktop-width) {
  .faq {
    padding-bottom: 170px;
  }

  .faq__title {
    padding-top: 124px;
    margin-left: 55px;

    &::before {
      left: -31px;
      top: 123px;
    }
  }

  .faq-lists-container {
    display: flex;
    margin-top: 60px;
    margin-left: 20px;
    margin-bottom: 4px;
  }

  .faq-list {
    .list-reset();
    width: 60%;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    margin: 0;

    &--second-list {
      margin-left: 30px;
    }
  }

  .faq-list li {
    margin-bottom: 10px;
  }

  .faq-form-block {
    width: 97.5%;
    margin-left: 23px;
    background-image: url("/img/background-faq-form-desktop.png");
    background-size: cover;

    &::before {
      position: absolute;
      content: "";
      top: 80px;
      left: 135px;
      background-image: url("/img/question-mark-desktop.png");
    }

    &::after {
      position: absolute;
      content: "";
      top: 52px;
      left: 94px;
      background-image: url("/img/small-ellips-desktop.png");
      animation:rotate 10s linear infinite;
    }
  }

  .faq-form__title {
    margin-left: 27%;
    color: #ffffff;
  }

  .faq-form__subtitle {
    margin-left: 27%;
  }

  .faq-form {
    width: 70%;
    display: flex;

    margin-top: 8px;
    margin-left: 25%;

    &::before {
      position: absolute;
      content: "";
      top: -74px;
      left: -212px;
      width: 154px;
      height: 154px;
      animation:rotate 10s linear infinite;
    }
  }

  .faq-form__input-name,
  .faq-form__input-phone {
    width: 96%;
  }

  .faq-form__input-phone {
    margin-left: 40px;
  }

  .faq-form__label-phone {
    left: 67px;
  }

  .faq-form__label-box--phone::before {
    left: 40px;
  }

  input:focus ~ .faq-form__label-box::before {
    width: 96%;
  }

  .faq-form__button {
    width: 24%;
    margin: 0;
    margin-top: 16px;
    margin-left: 74px;

    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
