.printing-house {
  min-height: 829px;
  background-image: url("/img/background-mobile.png");
  background-repeat: no-repeat;
  background-color: #9642eb;

  background-size: cover;
  min-height: auto;
  height: 829px;
  position: relative;

  margin-top: -47px;
  z-index: -1
}

.printing-house__image {
  margin-left: -15px;
  margin-top: 46px;
}

.printing-house__info-block {
  display: flex;
  flex-direction: column;
}

.printing-house__title {
   width: 220px;
  font-family: @muller;
  font-size: 36px;
  font-style: normal;
  font-weight: 800;
  line-height: 1.2;
  text-transform: uppercase;

  color: #ffffff;
  margin: 0;
}

.printing-house__description {
  font-family: @muller;
  font-size: 21px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;

  color: #ffffff;
  margin-top: 28px;
}

.printing-house__button {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 48px;

  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.9px;

  color: #ffffff;
  background-color: #fb0275;
  border-radius: 25px;

  margin-top: 46px;
  cursor: pointer;
  transition-duration: 0.2s;
}

.printing-house__small-text {
  display: block;
  width: 70%;
  font-family: @openSans;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;

  color: #ffffff;
  margin-top: 26px;
}

@media (min-width: @tablet-width) {
  .printing-house {
    background-image: url("/img/background-tablet.png");
    background-repeat: no-repeat;
    background-size: cover;
    min-height: auto;
    height: 600px;
    margin-top: -55px;
   }

   .printing-house__image {
    margin-top: 57px;
  }

  .printing-house__info-block {
    margin-top: 50px;
   }

  .printing-house__title {
    width: 290px;
    font-size: 45px;
    letter-spacing: 0.1px;

    margin: 0;
    margin-left: 10px;
    margin-top: 36px;
 }

 .printing-house__description {
    margin-left: 12px;
    margin-top: 33px;
  }

  .printing-house__button {
    width: 82%;
    height: 48px;

    font-family: @openSans;
    font-size: 16px;
    font-style: normal;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 0.9px;

    color: #ffffff;
    background-color: #fb0275;
    border-radius: 25px;

    margin-top: 40px;
    margin-left: 12px;
}
  .printing-house__small-text {
    margin-left: 13px;
  }
}

@media (min-width: @desktop-width) {
  .printing-house {
    background-image: url("/img/background-desktop.png");
    background-image: none;
    background-repeat: no-repeat;

    background-size: cover;
    min-height: auto;
    height: 941px;
    margin-top: -95px;

    z-index: 0;
 }

 .printing-house__image {
    margin-left: -87px;
    margin-top: 171px;
  }

  .printing-house__info-block {
    width: 50%;
    margin-left: 68px;
    margin-top: 220px;
    position: relative;
 }

  .printing-house__title {
    width: 380px;
    font-size: 60px;
    letter-spacing: 0.1px;

    margin: 0;
    margin-bottom: 50px;
    margin-left: 10px;
  }

  .printing-house__description {
    width: 430px;
    font-size: 32px;
    letter-spacing: 0.7px;

    margin: 0;
    margin-left: 10px;
    color: #ffffff;
  }

  .printing-house__button {
    width: 46%;
    height: 48px;

    margin-top: 76px;
    margin-left: 10px;

    &:hover,
    &:focus {
      color:#fb0275;
      background-color: #ffffff;
    }

    &:active {
      opacity: 0.5;
    }
  }

  .printing-house__small-text {
    position: absolute;
    width: 40%;
    top: 433px;
    left: 274px;
    letter-spacing: 0.2px;
  }
}
